const modal = {}

Modal.install = function(Vue) {
	/**
	 * 확인창 표시
	 * @param {*} title
	 * @param {*} text
	 */
	Vue.prototype.confirmModal = function(title, text) {
		let res = this.$bvModal.confirm({
			title: title,
			text: text,
			persistent: true,
			actions: {
				false: 'No',
				true: {
					color: 'red',
					text: 'Yes',
				},
			},
		})

		return res
	}
	/**
	 * 입력창 표시
	 * @param {*} title
	 * @param {*} text
	 */
	Vue.prototype.promptModal = function(title, text, content) {
		let res = this.$bvModal.prompt({
			title: title,
			text: text,
			value: content,
			persistent: true,
		})

		return res
	}
}

export default modal



